// Radio.tsx
import clsx from 'clsx';
import { focusRing } from '../../../tailwind/styles/focus';
import { RadioProps } from './radio.interface';

export const Radio: React.FC<RadioProps> = ({
  id,
  name,
  value = id,
  selected = false,
  required = false,
  disabled = false,
  error = false,
  label,
  tabIndex = 0,
  onChange,
  ...props
}) => {
  return (
    <span
      className={clsx(
        'zep-inline-flex',
        'zep-flex-row',
        'zep-flex-wrap',
        'zep-items-center',
        'zep-justify-start',
        'zep-relative',
        {
          '[&>input]:hover:zep-border-neutral-dark-hover [&>span]:hover:zep-bg-neutral-dark-hover': !disabled && !error,
        },
        { 'zep-cursor-not-allowed zep-opacity-disabled': disabled },
      )}
      data-testid="zep-radio"
    >
      {selected && (
        <span
          className={clsx(
            'zep-absolute',
            'zep-z-10',
            'zep-rounded-full',
            'zep-border-greyscale-0',
            { 'zep-left-[1px] zep-top-[1px] zep-h-[22px] zep-w-[22px] zep-border-[6px]': !error },
            { 'zep-left-[2px] zep-top-[2px] zep-h-[20px] zep-w-[20px] zep-border-[5px]': error },
            'rounded-full',
            [error ? 'zep-bg-error' : 'zep-bg-neutral-dark-default'],
          )}
        ></span>
      )}
      <input
        id={id}
        value={value}
        name={name}
        type="radio"
        tabIndex={tabIndex}
        checked={selected}
        required={required}
        onChange={!disabled ? onChange : undefined}
        className={clsx(
          focusRing,
          'zep-absolute',
          'zep-border-1',
          'zep-appearance-none',
          'focus-visible:zep-ring-offset-1',
          'zep-w-1.5',
          'zep-h-1.5',
          'zep-rounded-full',
          { 'zep-border-neutral-dark-default': !error && !selected },
          { 'zep-border-2': error },
          { 'zep-error zep-border-error': error && selected },
          { 'zep-border-error': error },
        )}
        disabled={disabled}
        data-testid="zep-radio-input"
        {...props}
      />
      <label
        htmlFor={id}
        className={clsx('zep-relative', 'zep-z-20', 'zep-pl-[36px]', [{ 'zep-cursor-pointer': !disabled }])}
      >
        {label}
      </label>
    </span>
  );
};
